import {Component, Input} from '@angular/core';
import {Route} from '@angular/router';

@Component({
  selector: 'bs-side-nav',
  template: `
    <ul class="nav nav-pills flex-column">
      <li class="nav-item" routerLinkActive="active" *ngFor="let item of items">
        <a class="nav-link" routerLink="{{item.url}}" routerLinkActive="active">{{item.title}}</a>
      </li>
    </ul>
  `,
  styleUrls: ['styles.scss']
})
export class BsSideNavComponent {

  @Input() routes: Route[];

  items = [];

  constructor() {
    this.items = this.routes.filter(route => route.component)
      .map(route => ({
        title: route.data.title,
        url: `/${route.path}`,
        data: route.data
      }));
  }
}


