import {ChangeDetectionStrategy, Component, OnInit} from '@angular/core';
import {BaseComponent} from '@er/core';
import {StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';

@Component({
  selector: 'bs-nav-page',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class BsNavPageComponent extends BaseComponent implements OnInit {

  title: string;

  ngOnInit() {
    this.title = StatesUtils.getValue(StateNames.appTitle);
  }

}
